.bg-pttern-all{
    //background-image: url(http://localhost:3000/assets/images/home/white-noise.png);
    background-position: 0 0,50% 50%;
    background-size: auto,359px;
    background-repeat: repeat,repeat;
    padding: 45px 0 30px;
    background-size: 100px 100px;
}
.jurisdictional-inner {
    padding: 40px 0px;
}
.jurisdictional-inner h1{
	color: #222;
    font-weight: 400;
    font-size: 24px;
    margin: 0px 0 25px 0;
    line-height: 34px;
    font-family: 'Poppins', sans-serif;
    text-transform: unset;
}
.jurisdictional-inner nav.nav.nav-tabs {
    margin: 0 auto;
    border: none;
    max-width: 70%;
    width: 100%;
    margin-bottom: 40px;
}
.jurisdictional-inner nav.nav.nav-tabs a.active {
    background-color: #d63d4b;
    border: none;
    border-radius: 0px;
}
.jurisdictional-inner nav.nav.nav-tabs a {
    width: 50%;
    text-align: center;
    color: #fff;
    font-weight: 400;
    font-size: 16px;
    font-family: 'Poppins', sans-serif!important;
    text-transform: unset;
    background-color: #333;
    padding: 10px;
    border: none;
    border-radius: 0px;
}
.first-tab table thead tr th{
    background-color: #333333;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    text-align: right;
    padding: 5px;
    line-height: 17px;
    border: none;
}
.first-tab table tbody tr td {
    color: #333;
    font-weight: 400;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    text-align: right;
    height: 40px;
    vertical-align: middle;
    border: none;
}
.first-tab table tbody tr td.pink-bg-heading {
	background-color: #d63d4b;
    font-family: 'Poppins', sans-serif!important;
    font-size: 16px;
    text-transform: inherit;
    font-weight: 700;
    color: #fff;
    text-align: left;
}
.first-tab table tbody tr td.pink-heading {
    color: #d63d4b;
    font-weight: 700;
    font-size: 16px;
    font-family: 'Poppins', sans-serif!important;
    text-transform: unset;
    padding: 5px 5px 1px 5px;
    text-align: left;
}

.first-tab table tbody tr td:first-child {
    padding-left: 25px;
    text-align: left;
}
.first-tab table tbody tr td:nth-child(1) {
    font-family: 'Lusitana';
}
.first-tab table tbody tr td:first-child, .first-tab table tbody tr td:last-child {
    font-weight: 600;
}
.first-tab  table tbody tr:nth-of-type(even) {
    background-color: #f1f0ec;
}
.first-tab  table tbody tr:nth-of-type(odd) {
    background-color: #fff;
}
.first-tab  table tbody tr:hover {
    background-color: #f9dea4;
}
/*****Note****/
.notes-area{
	background-color: #333333;
    color: #fff;
    font-weight: 400;
    font-family: 'Lusitana';
    line-height: 36px;
    margin-top: 50px;
    font-size: 16px;
    padding: 20px;
    margin: 50px 0px 25px;
}
.notes-area ul li {
    list-style: decimal;
    line-height: 22px;
}
.notes-area ul li span {
    font-weight: bold;
}
.pdf-btn button{
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    background-color: #d63d4b;
    max-width: 200px;
    width: 100%;
    color: #FFFFFF;
    height: 47px;
    border-radius: 0px;
    border: none;
}
.pdf-btn button:hover, .pdf-btn button:focus{
	background-color: #333;
	color: #fff;
}
.second-tab{
	display: flex;
	width: 100%;
	align-items: center;
}
.leftbox {
    width: calc(100% - 46%);
}
.comp-left-arrow {
    width: 6%;
    margin-top: 40px;
}
.rightbox {
    width: calc(100% - 46%);
}
.comp-right-arrow {
    width: 6%;
    margin-top: 40px;
}
.second-tab p {
    color: #000000;
    font-weight: 600;
    font-size: 13px;
    font-family: "Lato", sans-serif;
    text-transform: uppercase;
    height: 38px;
}
.second-tab ul {
    margin: 0;
    padding: 0;
    max-height: 280px;
    min-height: 280px;
    overflow-y: scroll;
    border: 1px solid #ddd;
}
.second-tab ul li {
    color: #000000;
    font-weight: 400;
    font-size: 13px;
    font-family: "Lato", sans-serif;
    padding: 0 10px;
    line-height: 27px;
    list-style: none;
    margin-bottom: 1px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    cursor: pointer;
}
.second-tab ul li:nth-of-type(odd) {
    background-color: #f4f4f4;
}
.second-tab ul li:nth-of-type(even) {
    background-color: #fafafa;
}
.second-tab ul li.active {
    color: #000000;
}
.second-tab ul li:hover, .bg-ptrn .second-tab ul li.active {
    background-color: #f3d79a;
}

.second-tab ul li.active {
    background-color: #f3d79a;
}



.comp-left-arrow span {
    height: 50px;
    width: 35px;
    display: block;
    margin: 10px auto;
    cursor: pointer;
        background-color: #d63d4b;
    color: #fff;
    padding: 2px 9px;
    font-size: 30px;
}
.comp-left-arrow span.right-side:hover {
    background-color: #333333;
}
.comp-left-arrow span.left-side:hover {
    background-color: #333333;
}
.comp-right-arrow span {
    background-repeat: no-repeat;
    height: 50px;
    width: 35px;
    display: block;
    margin: 10px auto;
    cursor: pointer;
    background-color: #d63d4b;
    color: #fff;
    padding: 2px 9px;
    font-size: 30px;
}
.comp-right-arrow span.top-side:hover {
    background-color: #333333;
}
.comp-right-arrow span.top-side {
    background-color: #d63d4b;
    color: #fff;
    padding: 2px 9px;
    font-size: 30px;
}
.comp-right-arrow span.bottom-side:hover {
    background-color: #333333;  
}
.comp-right-arrow span.bottom-side {
    background-color: #d63d4b;
    color: #fff;
    padding: 2px 9px;
    font-size: 30px;
}
.genrate-btn button{
	width: calc(100% - 60px);
	font-size: 16px;
    font-family: 'Poppins', sans-serif;
    background-color: #d63d4b;
    margin-top: 25px;
    color: #FFFFFF;
    font-weight: 400;
    border: 0;
    height: 47px;
    cursor: pointer;
    padding: 0 20px;
}
.genrate-btn button:hover, .genrate-btn button:focus{
	background-color: #333;
	color: #fff;
}
button{
	transition: 0.5s;
}
.regional-table {
    margin-top: 30px;
}
.regional-table h4 {
    margin-bottom: 10px;
    color: #222;
    font-weight: 700;
    font-size: 19px;
    font-family: 'Poppins', sans-serif!important;
}
.regional-table table {
    width: 100%;
}
.table-all thead th {
    background-color: #333333;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    font-weight: 700;
    color: #fff;
    text-align: right;
    padding: 10px 5px 5px 5px;
    line-height: 17px;
    border: none;
}
.table-all tbody tr td:first-child {
    padding-left: 25px;
    text-align: left;
}
.table-all tbody tr td {
    color: #333;
    font-weight: 400;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    text-align: right;
    height: 40px;
    vertical-align: middle;
    border: none;
    padding: 5px;
}
.table-all tbody tr td:nth-child(1) {
    font-family: 'Lusitana';
}
.table-all tbody tr td:first-child, .table-all tbody tr td:last-child {
