}
.regional-table table thead tr th:nth-of-type(3), .regional-table table thead tr th:nth-of-type(4), .regional-table table thead tr th:nth-of-type(5), .regional-table table thead tr th:nth-of-type(6), .regional-table table thead tr th:nth-of-type(7) {
    width: 13%;
}
.fullregional-box {
    margin-top: 25px;
}
.go_fullregional a {
    color: #fff;
    font-family: 'Poppins', sans-serif!important;
    text-transform: unset;
    text-decoration: none;
    background-color: #333;
    padding: 10px 30px;
    line-height: 38px;
}