body{
  		margin: 0 auto;
  		font-family: 'Poppins', sans-serif;
  	}
  	header {
	    background-color: #003253;
	    padding: 12px 0px;
	}
	.container-big {
		max-width: 1250px;
		width: 100%;
		margin: 0 auto;
		padding: 0px;
	}
	header .header-left ul{
		padding: 0px;
		margin: 0px;
	}
	header .header-left ul li {
	    float: unset;
	    list-style: none;
	    padding: 10px;
	    margin-right: 2px;
	    background: transparent;
	    color: #fff;
	    display: block;
	    clear: both;
	}
	header .header-left ul li a {
    	float: left;
	    list-style: none;
	    color: #fff;
	    font-size: 18px;
	    text-decoration: none;
	    position: relative;
	}
	header .header-left ul li a p{
	    font-size: 14px;
	    margin-bottom: 0;
	    line-height: .5;
	}
	header .header-left ul li a span {
	    font-weight: bold;
	    font-size: 18px;
	    text-transform: uppercase;
	}
	header .header-left ul li a:before {
	    display: inline-block;
	    content: "";
	    position: absolute;
	    width: 4px;
	    height: 30px;
	    background-color: #D63D4B;
	    left: -13px;
	    top: -2px;
	}
	.header-right h4 {
	    font-weight: 400;
	    font-size: 22px;
	    text-transform: capitalize;
	    color: #fff;
	    text-align: right;
	    margin-bottom: 0px;
	}
	.header-right h2 {
	    font-size: 24px;
	    font-weight: 700;
	    color: #fff;
	    margin: 0px;
	}
	nav.navbar {
		padding: 0px;
		margin-left: 10px;
	}
	nav.navbar button {
	    padding: 0px;
	    border-radius: 0px;
	    border: none;
	}
	nav.navbar button:focus{
		outline: none;
		box-shadow: none;
	}
	nav.navbar button span.navbar-toggler-icon {
		background-image: url(http://162.255.134.180:3000/assets/images/header-sprite.svg);
		background-position: 0 -54px!important;
		height: 50px;
		width: 35px;
		background-size: 226px;
	}
	nav.navbar .navbar-collapse{
		position: absolute;	
		top: 100%;	
		width: 335px;
		right: 0;
		z-index: 10;
	}
	nav.navbar .navbar-collapse .navbar-nav{
		background-color: #858585;			
		border: 1px solid #fff;
	}
	nav.navbar .navbar-collapse .navbar-nav a.nav-link{
		border-bottom: 1px solid #fff;
		color: #fff;
		font-weight: 400;
		font-size: 18px;
		font-family: "Lato",sans-serif;
		padding: 10px 10px;
		display: block;
	}
	nav.navbar .navbar-collapse .navbar-nav a.nav-link:hover{
		background: #D63D4B;
	}

	nav.navbar button:hover ~ .navbar-collapse{
		display: block;
	}

	nav.navbar:hover .navbar-collapse{
		display: block;
	}
