.econimic-charts .card {
    padding: 22px 20px 15px;
    background-color: #eeece7;
    border-radius: 0px;
    border: none;
}
.econimic-charts .card-body{
	padding: 0px;
}
.econimic-charts .card-title.h5 {
    text-align: center;
    color: #333;
    font-weight: 400;
    font-size: 14pt;
    font-family: 'Poppins', sans-serif;
    text-transform: unset;
    border-bottom: 2px dotted #bebdb9;
    padding-bottom: 12px;
    margin-bottom: 18px;
}
.econimic-charts .blockquote {
    margin-top: 14px;
    margin-bottom: 0;
    border-top: 2px dotted #bebdb9;
    padding-top: 12px;
}
.econimic-charts .blockquote-graph {
    width: 100%;
    height: 207px;
    text-align: left;
    overflow: hidden;
}
.econimic-charts .blockquote-graph img{
	width: 100%;
	height: 207px;
}
.econimic-charts .blockquote ul{
	text-align: right;
}
.econimic-charts .blockquote ul li {
    padding: 0 0 0 25px;
    list-style: none;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
}
.econimic-charts .blockquote ul li h4 {
    color: #333;
    font-weight: 600;
    font-size: 14px;
    font-family: 'Poppins', sans-serif;
    line-height: 25px;
    text-transform: capitalize;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 700;
}
.econimic-charts .blockquote ul li h5 {
    color: #000;
    font-weight: 400;
    font-size: 12px;
    font-family: Lusitana;
    line-height: 18px;
    font-style: italic;
    margin-top: 10px;
    margin-bottom: 10px;
}
.econimic-charts .blockquote  ul li h2 {
    color: #333;
    font-weight: 700;
    font-size: 44px;
    font-family: Lusitana;
    line-height: 55px;
    padding: 0;
    display: inline-block;
    vertical-align: middle;
    margin-top: 20px;
    margin-bottom: 10px;
}
.econimic-charts .source.upper_source {
    text-align: left;
    font-family: Lusitana;
    font-style: italic;
    font-weight: 400;
    margin: 10px 0 0;
    color: #333;
    padding: 0;
    font-size: 16px;
}
p.source.upper_source {
    margin-top: 15px;
}
.economic-table {
    margin: 28px 0px 0px;
    border-top: 2px dotted #bebdb9;
    padding-top: 28px;
}
.economic-table table{
	border-bottom: 2px solid #333;
    margin-bottom: 0px;
}
.economic-table h2{
	color: #222;
    font-weight: 400;
    font-size: 25px;
    margin: 0 0 15px 0;
    line-height: 34px;
    font-family: Poppins,sans-serif;
    text-align: left;
}
.economic-table table thead tr:nth-of-type(1) th {
    background-color: #333;
    padding: 4px 10px;
    font-family: Poppins,sans-serif;
    font-size: 15px;
    font-weight: 700;
    height: 40px;
    vertical-align: middle;
    color: #fff;
    text-align: center;
}
.economic-table table thead tr:nth-of-type(1) th:nth-of-type(1) {
    border-right: 1px solid #fff;
}
.economic-table table thead tr:nth-of-type(2) th {
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    vertical-align: middle;
    font-weight: 700;
    background-color: #222!important;
    height: 47px;
    color: #fff;
    text-align: right;
}
.economic-table table thead tr:nth-of-type(2) th:nth-of-type(2) {
    border-right: 1px solid #fff;
}
.economic-table table thead tr th{
	border: none;
}
.economic-table table tbody tr:first-child, .economic-table table tbody tr:first-child:hover{
    background-color: #d63d4b!important;
}
.economic-table table tbody tr:first-child td{
	color: #fff;
	font-weight: 600;
}
.economic-table table tbody tr td:nth-of-type(1) {
    text-align: left;
    font-weight: 700;
    font-family: Lusitana;
}
.economic-table table tbody tr td {
    font-family: 'Poppins', sans-serif;
    height: 40px;
    text-align: right;
    color: #333;
    font-weight: 400;
    font-size: 16px;
    border: none;
}
.economic-table table tbody tr:nth-of-type(even) {
    background-color: #f1f0ec;
}
.economic-table table tbody tr:nth-of-type(odd) {
    background-color: #fff;
}
.economic-table table tbody tr.active, .economic-table table tbody tr:hover {
    background-color: rgba(255,191,59,.4)!important;
    cursor: pointer;
}
