.real-estate {
    margin: 34px 0px 0px;
    border-top: 2px dotted #bebdb9;
    padding-top: 28px;
}
.real-estate h2{
	color: #222;
    font-size: 25px;
    margin: 0 0 15px 0;
    line-height: 34px;
    font-family: Poppins,sans-serif;
    text-align: left;
    font-weight: 700;
}
.real-estate .card-title.h5 {
    color: #333;
    font-weight: 700;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
    border: none;
    text-transform: capitalize;
    line-height: unset;
    margin-bottom: 0;
    text-align: center;
    padding-bottom: 10px;
}
.real-estate .card-title.h5 span{
    font-size: 14px;
    font-weight: 400;
    font-family: Lusitana;
    display: block;
}
.real-estate .card{
    padding: 15px;
    background-color: #eeece7;
    border: none;
    border-radius: 0px;
}
.real-estate .card-body{
    padding: 0px;
}
.real-estate blockquote {
    padding: 10px 5px 20px;
    border-top: 2px dotted #bebdb9;
    text-align: center;
}
.real-estate blockquote h5 {
    margin: 10px 0px;
    color: #333;
    font-size: 14px;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
    text-transform: unset;
}
.real-estate blockquote h4 {
    margin: 10px 0px;
    font-weight: 400;
    font-size: 50px;
    font-family: Lusitana;
    line-height: 60px;
    text-align: center;
    color: #222;
}
.real-estate .map-content-area{
    display: flex;
    align-items: center;
}
.real-estate .map-content-area .map-sctn{
    width: 50%;
    height: 32px;
    overflow: hidden;
    text-align: left;
}
.real-estate .map-content-area ul {
    padding: 0px;
    list-style: none;
    padding-left: 10px;
    width: 50%;
    margin: 0px;
}
.real-estate .map-content-area ul li {
    color: #333;
    font-weight: 400;
    font-size: 16px;
    font-family: Lusitana;
    line-height: 23px;
    text-align: left;
    position: relative;
    padding-left: 24px;
}
.real-estate .map-content-area ul li .fas {
    font-size: 22px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}
