    border: none;
    padding: 15px 20px;
    background-color: #eeece7;
    height: 330px;
}
.housing-market .card-title.h5{
    color: #333;
    margin-top: 0;
    line-height: unset;
    font-weight: 700;
    font-size: 17px;
    font-family: 'Poppins', sans-serif;
