}
.housing-map .housing-sec h4 {
    color: #0e0e1c;
    font-weight: 400;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    line-height: 20px;
    margin: 15px 0px;
    text-transform: capitalize;
}
.housing-map .hosuing-sec-map img{
    width: 100%;
    height: 145px;
}

p.source.text-left {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    margin: 10px 0 0;
}
p.source.text-left .btns-median {
    margin-top: 0;
    width: 100%;
    margin-bottom: 10px;
    margin-left: 46px;
    display: block;
}
p.source.text-left .btns-median a {
    text-align: left;
    color: #333;
    font-weight: 400;
    font-size: 12px;
    font-family: 'Poppins', sans-serif;
    display: inline-block;
    min-width: 110px;
    padding: 0;
    font-style: unset;
    position: relative;
    margin-left: 23px;
}
p.source.text-left .btns-median a::before {
    position: absolute;
    content: "";
    left: -29px;
    width: 23px;
    height: 3px;
    top: 7px;
}
p.source.text-left .btns-median a.first::before {
    background-color: #d63d4b;
}
p.source.text-left .btns-median a.second::before {
    background-color: #6ba2b9;
}
