.workforce-inner {
    padding: 55px 0px 30px;
    font-family: 'Poppins', sans-serif;
}
.workforce-inner h1{
	color: #424242;
    font-weight: 400!important;
    font-size: 40px;
    text-transform: none;
    line-height: 34px;
    display: inline-block;
    font-weight: 700;
    padding-bottom: 36px;
    text-align: center;
    width: 100%;
    margin: 0 0 45px 0;
    border-bottom: 4px dotted #b2aea2;
    font-family: 'Poppins', sans-serif;
}    
.workforce-inner h1 strong{
	font-weight: 700!important;
}
.workforce-sec {
    margin-top: 20px;
    border-bottom: 4px dotted #b2aea2;
    padding: 10px 0 30px;
}
.workforce-sec h4{
    color: #222;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    margin: 10px 0px;
}
.workforce-sec h4 span{
	color: #000;
    font-weight: 400;
    font-size: 16px;
    font-family: Lusitana,serif;
}
/****/
.workforce-sec-full {
    display: flex;
    margin-top: 30px;
}
.workforce-sec-left{
	width: 75%;
}
.workforce-sec-left .workforce-graph-img {
    height: 400px;
    width: 100%;
    overflow: hidden;
    text-align: left;
}
.workforce-sec-left .right_{
	color: #222;
    font-weight: 400;
    font-size: 14px;
    font-style: italic;
    font-family: Poppins,sans-serif;
    margin: 20px 0;
}
.workforce-sec-right {
    width: 24%;
    margin-left: 1%;
    padding: 10px;
    background-color: #dfdaca;
}
.workforce-sec-right h4 {
    color: #222;
    font-weight: 700;
    font-size: 19px;
    line-height: 26px;
    text-align: left;
    margin: 20px 0 10px;
}
.workforce-sec-right h6{
	color: #333;
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    font-family: Lusitana,serif;
    text-align: left;
    border-bottom: 4px dotted #b2aea2;
    padding-bottom: 5px;
}
.workforce-sec-right .inner-box {
    text-align: center;
    width: 100%;
    border-bottom: 4px dotted #b2aea2;
    padding-bottom: 15px;
    margin: 0 0 20px 0;
}    
.workforce-sec-right .inner-box h2{
	color: #333;
	font-weight: 400;
	font-size: 50px;
	font-family: Lusitana,serif;
	line-height: 65px;
	display: block;
	margin: 15px 0 10px 0;	
}
.workforce-sec-right .inner-box h5{
	color: #333;
	font-weight: 400;
	font-size: 32px;
	font-family: Lusitana,serif;
	line-height: 30px;
	width: 100%;
	display: flex;
    align-items: center;
    justify-content: center
}
.workforce-sec-right .inner-box .ranking-style{
	margin-right: 4px;
}
.workforce-sec-right .inner-box span{
	color: #333;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    width: 100%;
    vertical-align: middle;
    font-family: Lusitana,serif;
    padding-bottom: 10px;
}
.workforce-sec-right .graph-sec {
    height: 50px;
    overflow: hidden;
    text-align: left;
    margin: 10px 0px;
}
.workforce-sec-right .graph-sec img{
	width: 100%;
	height: 50px;
}
.workforce-sec-right ul.graph-indicator{
	padding: 0px;
	margin: 0px;
}
.workforce-sec-right ul.graph-indicator li{
    list-style: none;
    width: auto;
    color: #333;
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    font-family: Lusitana,serif;
    padding: 5px;
    cursor: pointer;
    text-align: left;
}
.workforce-sec-right ul.graph-indicator li .bx {
    display: inline-block;
    vertical-align: middle;
    height: 16px;
    width: 42px;
    margin-right: 5px;
    margin-top: -4px;
    border: 1px solid #fff;
}
.workforce-sec-right ul.graph-indicator li .bx.grey-bx {
    background-color: #f4be49;
}
.workforce-sec-right ul.graph-indicator li .bx.pink-bx {
    background-color: #d63d4b;
}
.workforce-sec-right ul.graph-indicator li.active {
    background-color: #b2a99b;
}
.workforce-inner .note{
	color: #fff;
    font-weight: 400;
    font-size: 16px;
    font-style: normal;
    font-family: Lusitana,serif;
    margin-top: 30px;
    background: #333;
    padding: 30px;
}
.workforce-inner .note p{
	margin: 0px;
}