/*Slider and Logo section*/
.logo-sec{
		padding: 15px;
		background-color: #fff;
		height: 580px;
	}
	.logo-sec img{
	    text-align: center;
	    margin: 0 auto;
	    display: block;
	}
	.logo-sec p{
		color: #333;
	    font-weight: 300;
	    font-size: 17px;
	    font-family: 'Lusitana';
	    line-height: 22px;
	}
	.logo-sec h1{
		color: #222;
	    font-weight: bold;
	    font-size: 41px;
	    font-family: 'Poppins',sans-serif;
	    line-height: 40px;
	    margin-bottom: 5px;
	    line-height: 1.2;
        margin-top: 20px;
	}
	.logo-sec .btn-group {
	    width: 100%;
	}
	.logo-sec .btn-group .btn{
		width: 100%;
		background-color: #D63D4B;
	    border: none;
	    border-radius: 0px;
		color: #fff;
		font-weight: 400;
		font-size: 15px;
		font-family: 'Poppins',sans-serif;
		text-transform: capitalize;
		background-repeat: no-repeat;
		background-image: url(https://communitydashboard.vegas/assets/img/arrow-up.png);
		background-position: right 10px center;
		position: relative;
		padding-right: 44px;
		height: 47px;
		border: none;
	}
	.logo-sec .dropdown-toggle.btn-down {
 	   background-image: url(https://communitydashboard.vegas/assets/img/arrow-down.png)!important;
	}		
	.logo-sec .btn-group .btn:focus, .logo-sec .btn-group .btn:active{
		outline: none!important;
		border: none!important;
		box-shadow: none!important;
		background-color: #D63D4B;
	}	
	.logo-sec .dropdown-toggle::after{
		width: 2px;
	    height: 100%;
	    top: 0;
	    right: 37px;
	    background-color: #fff;
	    position: absolute;
	    border: none;
	}
	.logo-sec .dropdown-menu {
	    background-color: #333;
	    padding: 0px;
	    border-radius: 0px;
	    transform: none!important;
	    top: 100%!important;
	    border: none;
	    margin-top: 1px;
	    transition: all 0.8s ease;
	    width: 100%
	}
	.logo-sec .dropdown-menu ul li {
	    list-style: none;
	    border-bottom: 1px solid #fff;
	}	
	.logo-sec .dropdown-menu ul li:last-child{
		border: none;
	}
	.logo-sec .dropdown-menu ul {
		padding: 0px;
		margin: 0px;
	}
	.logo-sec .dropdown-menu ul li a{
		color: #fff;
	    font-weight: 400;
	    font-size: 16px;
	    font-family: "Lato",sans-serif;
	    padding: 8px 10px;
	    display: block;
	}
	.logo-sec .dropdown-menu ul li a:hover, .logo-sec .dropdown-menu ul li a:focus{
		background-color: #D63D4B;
		color: #fff;
	}
	.home-crousel{
		background-color: #dfdaca;
	}
	.home-crousel .carousel-item{
		height: 580px;
	}
	.home-crousel .card-box {
	    background-color: #DFDACA;
	    height: 580px;
	    padding: 20px 60px 20px 60px;
	}
	.home-crousel .upper_sec h4 {
		font-family: 'Poppins',sans-serif;
		font-weight: 700;
		font-size: 29px;
		margin-top: 20px;
		margin-bottom: 10px;
	}
	.home-crousel .both-sec {
	    display: flex;
	    align-items: center;
	}
	.home-crousel .fx-direction label {
	    background: #EFB948;
	    color: #000;
	    padding: 4px;
	    margin: 25px 0;
	    display: inline-block;
	    font-size: 16px;
	    font-family: 'Poppins',sans-serif;
	}
	.home-crousel .fx-direction h2 {
	    font-family: 'Lusitana';
	    font-weight: 500;
	    font-size: 120px;
	    margin-top: 0;
	    line-height: 1;
	}
	.home-crousel .flx-dir {
	    flex-direction: column;
	    align-items: flex-start;
	}
	.home-crousel .both-sec.flx-dir .second-sec {
	    align-self: flex-start;
	    width: 100%;
	    text-align: left;
	}
	.home-crousel .both-sec .second-sec {
	    width: 40%;
	    text-align: center;
	    align-self: flex-end;
	}
	.home-crousel .both-sec .second-sec.large_img {
    	width: 100%;
	}
	.home-crousel .both-sec .first-sec {
 	   width: 60%;
	}
	.home-crousel .both-sec .second-sec img {
 	   max-width: 100%;
	}
	.home-crousel .first-sec p {
	    font-family: 'Lusitana';
	    font-weight: 500;
	    font-size: 18px;
	    margin-bottom: 0;
	}
	.home-crousel .carousel-indicators li{
		background-color: #A29F96;
		width: 14px;
	}
	.home-crousel .carousel-indicators .active {
 	   background-color: #D44354;
	}
	.home-crousel .carousel-control-next, .home-crousel .carousel-control-prev {
	    opacity: 1!important;
	    padding: 0px!important;
	    width: 46px;
	}
	.home-crousel .carousel-control-next span, .home-crousel .carousel-control-prev span {
	    font-weight: 100!important;
	    background-size: 30px;
	    width: 50px;
	    height: 50px;
	}
	.chart_hover h3 {
	    font-size: 18px;
	    font-weight: 600;
	    font-family: 'Poppins',sans-serif;
	}
	.data_show {
	    display: flex;
	    align-items: center;
	    width: 100%;
	}
	.data_show p.label_sec {
	    text-align: left;
	    margin-bottom: 0px;
	    font-size: 15px;
    	font-family: 'Lusitana';
	}
	.data_show p.value_sec {
	    margin-left: auto;
	    margin-bottom: 0px;
	    font-size: 15px;
        font-family: 'Lusitana';
        font-weight: 600;
	}
	.home-crousel .card-box a {
	    color: #333;
	    text-decoration: none;
	}
	.home-crousel .card-box a:hover, .home-crousel .card-box a:focus{
		color: #333;
	    text-decoration: none;
	}

/*-------------------------------Boxes Section-------------------------------------------*/
.bg-pattern{
	    background-image: url(https://communitydashboard.vegas/assets/img/white-noise.png);
	    background-position: 0 0,50% 50%;
	    background-size: auto,359px;
	    background-repeat: repeat,repeat;
	    padding: 45px 0 30px;
	    background-size: 100px 100px;
	}
	.rs_box_sec {
    	margin-top: 20px;
	}
	.rs_box_sec ul {
	    list-style-type: none;
	    display: flex;
	    flex-wrap: wrap;
	    justify-content: space-between;
	    padding: 0px;
	    margin: 0px;
	}
	.rs_box_sec ul li {
	    display: inline-block;
	    background-color: #EEECE7;
	    padding: 10px;
	    width: calc(20% - 20px);
    	margin: 12px 0;
    	text-align: center;
	}
	.rs_box_sec ul li a {
 	   text-decoration: none;
	}
	.rs_box p {
	    font-size: 18px;
	    color: #333;
	}