.container{
		padding: 0 30px;
    	max-width: 1250px!important;
    	width: 100%;
	}
	footer {
    	background-color: #003253;
    	padding: 30px 0px;
	}	
	footer h2 {
	    color: #fff;
	    position: relative;
	    margin-bottom: 30px;
	    font-weight: 600;
    	font-size: 20px;
    	text-transform: uppercase;
	}
	footer h2 span{
		font-size: 14px;
		font-weight: normal;
	}
	footer h2:before {
	    display: inline-block;
	    content: "";
	    position: absolute;
	    width: 4px;
	    height: calc(100% - 10px);
	    background-color: #D63D4B;
	    left: -13px;
	    top: 7px;
	}
	footer ul {
		margin-bottom: 30px;
		width: 100%;
		padding-left: 0px;
		float: left;
		width: 800px;
	}
	footer ul li {
	    list-style: none;
	    width: 50%;
	    display: inline-block;
	    float: left;
	}
	footer ul li a {
		font-family: 'Poppins',sans-serif;
		font-size: 14px;
		text-decoration: none;
		color: #fff;
		font-weight: 400;
	    position: relative;
	}
	footer ul li a:hover, footer ul li a:focus{
		text-decoration: none;
		color: #fff;
	}
	footer ul li a:after {
	    content: "";
	    height: 1px;
	    background-color: #fff;
	    left: 0;
	    bottom: -2px;
	    position: absolute;
	    width: 0;
	    -webkit-transition: all .5s;
	    -moz-transition: all .5s;
	    -ms-transition: all .5s;
	    -o-transition: all .5s;
	    transition: all .5s;
	}
	footer ul li a:hover:after {
	    width: 100%;
	}
	.copyright p{
		color: #fff;
	    font-weight: 400;
	    font-size: 12px;
	    font-family: "Lato",sans-serif;
	    margin: 0px;
	}
	.copyright p img {
	    max-width: 10px;
	    margin: 0 2px;
	}
	.copyright p a, .copyright p a:hover, .copyright p a:focus{
		color: #fff;
		text-decoration: underline;
	}